import * as React from 'react';
import { IPlaceholderProps } from './IPlaceholderProps';
import { Button, ButtonType } from 'office-ui-fabric-react/lib/components/Button';
import { css } from 'office-ui-fabric-react/lib/utilities/css';
import './Placeholder.scss';

/**
 * Component used to place holder user experience in the web part display area.
 *
 * @alpha
 */
/* tslint:disable:no-any */
export default class Placeholder extends React.Component<IPlaceholderProps, void> {
/* tslint:enable:no-any */
  private _placeholderButton: Button;
  private _descriptionId: string;

  /**
   * Render the place holder user experience.
   */
  public render(): React.ReactElement<IPlaceholderProps> {
    this._descriptionId = `description-${Math.random().toString()}`;
    return (
      <div className={ css('Placeholder', this.props.contentClassName) }>
        <div className='Placeholder-container ms-Grid'>
          <div className='Placeholder-head ms-Grid-row'>
            <div className='ms-Grid-col ms-u-hiddenSm ms-u-md3' />
            <div className='Placeholder-headContainer ms-Grid-col ms-u-sm12 ms-u-md6'>
              <i className={ 'Placeholder-icon ms-fontSize-su ms-Icon ' + this.props.icon }></i>
              <span className='Placeholder-text ms-fontWeight-light ms-fontSize-xxl'>{ this.props.iconText }</span>
            </div>
            <div className='ms-Grid-col ms-u-hiddenSm ms-u-md3' />
          </div>
          <div className='Placeholder-description ms-Grid-row'>
            <span className='Placeholder-descriptionText' id={ this._descriptionId }>
              { this.props.description }
            </span>
          </div>
          <div className='Placeholder-description ms-Grid-row'>
            { this.buttonElement }
          </div>
        </div>
      </div>
    );
  }

  /**
   * Render the button element.
   */
  public get buttonElement(): React.ReactElement<React.HTMLProps<HTMLDivElement>>{
    // VSO:Bug 227377 There is a bug in fabric react button that it is not assigning the correct id to
    // aria-labelledBy that confuses the screen reader. Added an extra aria-label to fix that
    return this.props.buttonLabel
      ? (<Button
          ref={ (c: Button) => this._placeholderButton = c }
          buttonType={ ButtonType.primary }
          onClick={ this.props.onAdd }
          ariaLabel={ this.props.buttonLabel }
          ariaDescription={ this.props.description }>
          { this.props.buttonLabel }
        </Button>)
      : undefined;
  }

  /**
   * Focus the button element.
   */
  public focusOnPlaceHolderButton(): void {
      if (this._placeholderButton) {
          this._placeholderButton.focus();
      }
  }
}